define('cute-cats-tanisha/templates/components/clinical/addon-test-support/unit/repositories/diagnoses-test', ['clinical/repositories/diagnoses', 'qunit', 'ember-sinon-qunit/test-support/test', 'boot/util/pf-server'], function (_diagnoses, _qunit, _test, _pfServer) {
    'use strict';

    (0, _qunit.module)('Unit - Core - Clinical | Repository - diagnoses tests');

    (0, _test.default)('searchForFamilyHistory calls v3 diagnosis/typeSearch with the filter and familyHealthHistorySearch flag', function (assert) {
        let promiseStub = this.stub(_pfServer.default, 'promise').returns(new Ember.RSVP.resolve([]));
        _diagnoses.default.searchForFamilyHistory('Diabetes');

        let url = promiseStub.getCall(0).args[0];
        assert.ok(url.match(/diagnosis\/typeSearch\/\?searchTerm=Diabetes&familyHealthHistorySearch=true/));
    });

    (0, _test.default)('searchForFamilyHistory sets diagnosisType for "Other diagnosis" and "Family diagnosis"', function (assert) {
        let relativesList = [{ relative: { observations: [{ diagnosis: { id: 1, snomedCode: 1 } }, { diagnosis: { id: 2, snomedCode: 2 } }] } }, { relative: { observations: [{ diagnosis: { id: 3, snomedCode: 3 } }] } }];
        this.stub(_pfServer.default, 'promise').returns(new Ember.RSVP.resolve([{ id: 4 }]));
        return _diagnoses.default.searchForFamilyHistory('Diabetes', relativesList).then(results => {
            assert.equal(results.length, 4);
            assert.equal(results.findBy('id', 1).diagnosisType, 'Family diagnosis');
            assert.equal(results.findBy('id', 2).diagnosisType, 'Family diagnosis');
            assert.equal(results.findBy('id', 3).diagnosisType, 'Family diagnosis');
            assert.equal(results.findBy('id', 4).diagnosisType, 'Other diagnosis');
        });
    });

    (0, _test.default)('searchForFamilyHistory returns Family diagnosis first and then Other diagnosis', function (assert) {
        let relativesList = [{ relative: { observations: [{ diagnosis: {} }] } }];
        this.stub(_pfServer.default, 'promise').returns(new Ember.RSVP.resolve([{}]));
        return _diagnoses.default.searchForFamilyHistory('Diabetes', relativesList).then(results => {
            assert.equal(results.length, 2);
            assert.equal(results[0].diagnosisType, 'Family diagnosis');
            assert.equal(results[1].diagnosisType, 'Other diagnosis');
        });
    });

    (0, _test.default)('searchForFamilyHistory de-dupes all Family Diagnosis by snomed', function (assert) {
        let relativesList = [{ relative: { observations: [{ diagnosis: { snomedCode: 1 } }, { diagnosis: { snomedCode: 2 } }] } }, { relative: { observations: [{ diagnosis: { snomedCode: 1 } }] } }];
        this.stub(_pfServer.default, 'promise').returns(new Ember.RSVP.resolve([]));
        return _diagnoses.default.searchForFamilyHistory('Diabetes', relativesList).then(results => {
            assert.equal(results.length, 2);
        });
    });
});