define('cute-cats-tanisha/templates/components/clinical/addon-test-support/unit/models/allergen-test', ['qunit', 'clinical/models/allergen'], function (_qunit, _allergen) {
    'use strict';

    (0, _qunit.module)('Unit - Core - Clinical | Model - Allergen');

    (0, _qunit.test)('Ingredients are classified into the correct group', assert => {
        const allergen = _allergen.default.create({
            allergenType: 'Class',
            name: 'Amoxicillin',
            rxNormCui: '723',
            score: 0.00336285075,
            probability: 1,
            isIngredient: true
        });

        assert.equal(allergen.get('group.display'), 'Ingredient');
        assert.equal(allergen.get('rxNormCui'), '723');
        assert.equal(allergen.get('name'), 'Amoxicillin');
    });

    (0, _qunit.test)('Classes are classified into the correct group when IsIngredient is false', assert => {
        const allergen = _allergen.default.create({
            allergenType: 'Class',
            name: 'Penicillins',
            score: 4.62047625,
            probability: 1,
            isIngredient: false
        });
        assert.equal(allergen.get('group.display'), 'Class');
    });

    (0, _qunit.test)('Classes are classified into the correct group when IsIngredient is not present', assert => {
        const allergen = _allergen.default.create({
            allergenType: 'Class',
            name: 'Penicillins',
            score: 4.62047625,
            probability: 1
        });
        assert.equal(allergen.get('group.display'), 'Class');
    });

    (0, _qunit.test)('Products are classified into the correct group', assert => {
        const allergen = _allergen.default.create({
            allergenType: 'PackagedDrug',
            name: 'Bicillin L-A',
            rxNormCui: '731572',
            score: 0.00336285075,
            ndc: '60793070210'
        });

        assert.equal(allergen.get('group.display'), 'Product');
        assert.equal(allergen.get('rxNormCui'), '731572');
        assert.equal(allergen.get('name'), 'Bicillin L-A');
        assert.equal(allergen.get('ndc'), '60793070210');
    });
});