        assert.equal((0, _getText.default)(`${MODAL_ELEMENT} .access-history-content .ember-view.row:first > div:nth(1)`), EXPECTED_FIRST_ITEM.providerName, 'Access history shows correct provider names');

        await click(PRINT_BUTTON);
        assert.ok(find('#print-modal:visible').length > 0, 'Print modal displays properly');
        // Validate print modal display information
        assert.equal((0, _getTextInPrint.default)((0, _dataElement.default)('print-header-patient-name')), expectedPatientName, 'Print modal has correct patient name');
