define('cute-cats-tanisha/templates/components/acceptance/print-diagnoses-test', ['qunit', 'boot/tests/helpers/data-element', 'boot/tests/helpers/module-for-acceptance-auth'], function (_qunit, _dataElement, _moduleForAcceptanceAuth) {
    'use strict';

    const PATIENT_GUID = 'ecd212c3-5c99-499e-b3c6-b2645b8a4c98';

    (0, _moduleForAcceptanceAuth.default)('Acceptance - Core - Charting | Summary diagnoses print test - v2');

    (0, _qunit.test)('Print diagnoses from encounter summary with term code and confirm print labels and headers', async assert => {
        server.post('ChartingEndpoint/api/v2/PrintAudit', () => null);
        visit('/PF/charts/patients/' + PATIENT_GUID + '/summary');
        await click(`${(0, _dataElement.default)('diagnosis-display-select')} .composable-select__choice`);
        await click((0, _dataElement.default)('diagnosis-display-select-option-0'));
        await click((0, _dataElement.default)('print-diagnoses-button'));
        assert.throws(findWithAssert(`${(0, _dataElement.default)('print-diagnoses')} .row.header-row > div:contains('Acuity')`), 'There is an Acuity header');

        // Confirm there is an acute/historical diagnoses row for Chronic and Acute diagnoses
        assert.equal($('.row.header-row').find(`div:contains('Historical Diagnoses')`).length, 1, 'There is a historical diagnosis section for diagnoses');
        assert.equal($('.row.header-row').find(`div:contains('Current Diagnoses')`).length, 1, 'There is an active diagnosis section for diagnoses');

        // Confirm start/stop row labels for one of the rows
        assert.throws(findWithAssert(`.row.header-row:contains('Current Diagnoses') div:contains('Start')`), 'There is Start column under current diagnoses');
        assert.throws(findWithAssert(`.row.header-row:contains('Current Diagnoses') div:contains('Stop')`), 'There is Stop column under current diagnoses');

        // Confirm diagnoses code, start and stop dates for one diagnosis
        assert.throws(findWithAssert(`${(0, _dataElement.default)('diagnosis-item-text')}:contains('Unspecified fracture')`), 'There is a diagnosis present in the print preview for Term Code');
        assert.equal(find(`${(0, _dataElement.default)('print-diagnosis-start-date')}:eq(1)`).text(), '10/03/2016', 'There is a diagnosis start date present');
        assert.equal(find(`${(0, _dataElement.default)('print-diagnosis-stop-date')}:eq(1)`).text(), '10/31/2016', 'There is a diagnosis stop date present');

        click('.close-link');
    });

    (0, _qunit.test)('Print diagnoses from encounter summary with Term', async assert => {
        server.post('ChartingEndpoint/api/v2/PrintAudit', () => null);
        await visit('/PF/charts/patients/' + PATIENT_GUID + '/summary');
        await click(`${(0, _dataElement.default)('diagnosis-display-select')} .composable-select__choice`);
        await click((0, _dataElement.default)('diagnosis-display-select-option-1'));
        await click((0, _dataElement.default)('print-diagnoses-button'));
        assert.throws(findWithAssert(`${(0, _dataElement.default)('diagnosis-item-text')}:contains('Broken femur')`), 'There is a diagnosis present in the print preview for Term');
        assert.equal(find(`${(0, _dataElement.default)('print-diagnosis-start-date')}:eq(0)`).text(), '11/03/2016', 'There is a diagnosis start date present');
        click('.close-link');
    });

    (0, _qunit.test)('Print diagnoses - acute active and historical', async assert => {
        server.post('ChartingEndpoint/api/v2/PrintAudit', () => null);
        visit('/PF/charts/patients/' + PATIENT_GUID + '/summary');
        await click(`${(0, _dataElement.default)('diagnosis-display-select')} .composable-select__choice`);
        await click((0, _dataElement.default)('diagnosis-display-select-option-0'));
        await click((0, _dataElement.default)('print-diagnoses-button'));
        assert.throws(findWithAssert(`${(0, _dataElement.default)('diagnosis-item-text')}:contains('(S72.90) Unspecified fracture')`).text(), 'There is an acute active diagnosis present in the print preview');
        assert.equal(find(`${(0, _dataElement.default)('print-diagnosis-start-date')}:eq(0)`).text(), '11/03/2016', 'There is a diagnosis start date present for acute active diagnosis.');
        assert.equal(find(`${(0, _dataElement.default)('print-diagnosis-stop-date')}:eq(0)`).text(), '', 'There is no diagnosis stop date present for acute active diagnosis.');

        assert.throws(findWithAssert(`${(0, _dataElement.default)('diagnosis-item-text')}:contains('(T14.8) Other injury')`).text(), 'There is an acute historical diagnosis present in the print preview');
        assert.equal(find(`${(0, _dataElement.default)('print-diagnosis-start-date')}:eq(1)`).text(), '10/03/2016', 'There is a diagnosis start date present for acute historical diagnosis.');
        assert.equal(find(`${(0, _dataElement.default)('print-diagnosis-stop-date')}:eq(1)`).text(), '10/31/2016', 'There is a diagnosis stop date present for acute historical diagnosis.');
        click('.close-link');
    });
});