        isSiteDisabled: Ember.computed.or('disabled', 'currentRoute.isSiteIrrelevant'),
        isVisConceptRequired: Ember.computed.and('model.isVisConceptRequired', 'isElectronicAndAdministered'),
        providerHasNpi: Ember.computed.or('providerNpiAdministering', 'providerNpiOrdering'),
